import React from "react";
import { Link } from "react-router-dom";
import {
  Grid,
  makeStyles,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import calendar from "../../assets/images/news/calendar.svg";
import button from "../../assets/images/news/button.svg";

const Card = ({ item, i18n }) => {
  const classes = useStyles();
  const theme = useTheme();
  const mobile = useMediaQuery(theme.breakpoints.down(576));
  const tablet = useMediaQuery(theme.breakpoints.down(992));

  return (
    <Link to={`/main/news/${item?.id}`}>
      <Grid
        container
        wrap="nowrap"
        className={classes.gridItem}
        direction={mobile ? "column" : "row"}
      >
        <div className="img">
          <img src={item?.photo} alt="" />
        </div>
        <Grid container wrap="nowrap" justifyContent="space-between">
          <div>
            {!mobile && (
              <Typography
                variant="body2"
                style={{
                  color: "#64748B",
                  display: "flex",
                  alignItems: "center",
                  marginTop: 20,
                }}
              >
                <img src={calendar} style={{ marginRight: 10 }} alt="" />
                {item?.date}
              </Typography>
            )}
            <Typography className={classes.title}>{item?.name}</Typography>
            {!mobile && (
              <Typography variant="body2" className={classes.subtitle}>
                {item?.body}
              </Typography>
            )}
          </div>
          {!mobile && (
            <img
              src={button}
              style={
                tablet ? { height: 150, marginLeft: 16 } : { marginLeft: 16 }
              }
              alt=""
            />
          )}
        </Grid>
      </Grid>
    </Link>
  );
};

export default Card;

const useStyles = makeStyles((theme) => ({
  gridItem: {
    borderBottom: "1px solid #D0D5DD",
    paddingBottom: 24,
    marginBottom: 20,

    "&:hover": {
      "& .img img": {
        transform: "scale(1.1)",
      },
    },

    "& .img": {
      width: 288,
      minWidth: 288,
      height: 192,
      borderRadius: 8,
      overflow: "hidden",
      marginRight: 32,
      "& img": {
        width: "100%",
        transition: "all .2s linear",
        height: "100%",
        objectFit: "cover",
      },

      [theme.breakpoints.down(992)]: {
        width: 150,
        minWidth: 150,
        height: 150,
        marginRight: 13,
      },

      [theme.breakpoints.down(576)]: {
        width: "100%",
        height: 190,
        minWidth: "100%",
        marginRight: 13,
      },
    },
  },
  title: {
    fontSize: 33,
    color: "#000",
    textOverflow: "ellipsis",
    overflow: "hidden",
    display: "-webkit-box",
    "-webkit-line-clamp": 2,
    "-webkit-box-orient": "vertical",
    wordWrap: "break-word",
    lineHeight: "37px",
    margin: "16px 0 17px",

    [theme.breakpoints.down(992)]: {
      fontSize: 28,
      lineHeight: "34px",
      margin: "9px 0 8px",
    },
  },
  subtitle: {
    fontSize: 18,
    color: "#000",
    textOverflow: "ellipsis",
    overflow: "hidden",
    display: "-webkit-box",
    "-webkit-line-clamp": 2,
    "-webkit-box-orient": "vertical",
    wordWrap: "break-word",
    lineHeight: "22px",

    [theme.breakpoints.down(992)]: {
      fontSize: 16,
      lineHeight: "19px",
    },
  },
}));
