import { Container, Typography } from "@material-ui/core";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import NewsCard from "../../components/NewsCard";
import { getNews } from "../../redux/reducers/infoReducer";

const News = ({ t }) => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getNews());
    // eslint-disable-next-line
  }, []);
  const { newsList } = useSelector(({ infoReducer }) => infoReducer);
  const arrayNews = Object.values(
    typeof newsList.news === "object" && newsList.news
  );
  return (
    <section className="whiteBg" style={{ color: "#000", padding: "80px 0" }}>
      <Container maxWidth="lg">
        <Typography variant="h2" style={{ marginBottom: 40 }}>
          {t("allNewsTitle")}
        </Typography>
        {arrayNews.map((item, idx) => (
          <NewsCard item={item} key={idx} />
        ))}
      </Container>
    </section>
  );
};

export default News;
